import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';

class Experience extends Component{
    render(){
        return(
        <Grid>
            <Cell col={4}>
                <p>{this.props.startYear}</p>
            </Cell>
            <Cell col={8}>
                <h4 style={{marginTop:'0px'}}>{this.props.jobName}</h4>
                <p>{this.props.jobDescription}</p>
                <p>{this.props.jobDescription2}</p>
                <p>{this.props.jobDescription3}</p>
                <p>{this.props.jobDescription4}</p>
                <p>{this.props.jobDescription5}</p>
                <p>{this.props.jobDescription6}</p>
                <p>{this.props.jobDescription7}</p>
            </Cell>
        </Grid>

        )
    }
}

export default Experience;